<!---->
<template>
<Frame title="">
<ul class="mui-table-view"> 
    <li class="mui-table-view-cell mui-collapse mui-active">
      <a href="#"></a>
      <div class="mui-collapse-content">
      ~<br/>
      ?<a href="#" @click="repick"></a>
      </div>
    </li>

    <li class="mui-table-view-cell mui-collapse mui-active">
      <a href="#">?</a>
      <div class="mui-collapse-content mui-row">
        <p>1.<font color="red">0</font></p>
        <p class="text-center" style="padding: 0.2rem">
          <img src="http://ugc.qpic.cn/gbar_pic/2wF3sr2LiaVs98SJS4wwgxrIApI8tv6pueWvaAW80D8xicz0Enrv9rnA/0" style="width: 3rem;height: 3rem" title=""/>
        </p>
        <div class="mui-row" v-if="IS_APP">
          <div class="mui-col-xs-1"></div>
          <div class="mui-col-xs-4">
            <button class="mui-btn btn-block" @click="saveQR"></button>
          </div>
          <div class="mui-col-xs-2"></div>
          <div class="mui-col-xs-4">
            <button class="mui-btn btn-block" @click="deleteQR"></button>
          </div>
        </div>
      <br/>
      <p>2.QQ 1540811286 </p>
      <br/>
      <ul>
        <li><font color="red"></font></li>
        <li><font color="red"></font></li>
      </ul>
      </div>
    </li>
  </ul>
</Frame>
</template>
<script>
import Frame from '@/components/Frame.vue';
export default {
  components: {
    Frame
  },
  data() {
    return {
      qr: 'telanx_zfb'
    };
  },
  methods: {
    saveQR() {
      window.zanhuang.jsAndroid(JSON.stringify({
        type: '',
        picName: this.qr + '.png',
        picUrl: 'http://ugc.qpic.cn/gbar_pic/2wF3sr2LiaVs98SJS4wwgxrIApI8tv6pueWvaAW80D8xicz0Enrv9rnA/0'
      }));
    },
    deleteQR() {
      window.zanhuang.jsAndroid(JSON.stringify({
        type: '',
        picName: this.qr + '.png',
      }));
    },
    repick() {
      localStorage['last_showdate'] = null;
      location.reload();
    }
  }
}
</script>
<style scoped>
p, li {
  line-height: 0.4rem;
}
</style>


